<template>
    <div>
         <multiplexing :swiper='swiper' :auto='auto'></multiplexing>
         <multiplexing :swiper='dyList' :auto='dy'></multiplexing>
         <multiplexing :swiper='listOut' :auto='list'></multiplexing>
    </div>
</template>
<script>
import multiplexing from "./multiplexing/multiplexing.vue";
export default {
  data() {
    return {
      auto: [],
      swiper: [
        {
          picInfo: []
        }
      ],
      dy: [],
      dyList: [
        {
          picInfo: []
        }
      ],
      list: [],
      listOut: [
        {
          picInfo: []
        }
      ]
    };
  },
  components: {
    multiplexing
  },
  methods: {
    init() {
      this.$indicator.open();
      this.$http.get(`https://www.apiopen.top/journalismApi`).then(res => {
        console.log(res.data.data);
        this.swiper = res.data.data.auto.splice(0, 1);
        this.auto = res.data.data.auto;
        this.dyList = res.data.data.dy.splice(0, 1);
        this.dy = res.data.data.dy;
        this.listOut = res.data.data.sports.splice(0, 1);
        this.list = res.data.data.sports;
        this.$indicator.close();
      });
    }
  },
  created() {
    this.init();
  }
};
</script>

<style scoped lang='less'>
</style>



