<template>
    <div>
        <div class="fixed">
        <header class="header">
            <img class="logo" src="data:image/svg+xml;base64," alt="">
             <p class="text">洪荒之力APP</p>
        </header>
            <ul class="list">
                <router-link tag='li' :to='{name:"menu"}'   :class="[index==0?'pink':'']"> <p @click='index=0'>内涵段子</p></router-link>
                <router-link tag='li' :to='{name:"admin"}'  :class="[index==1?'pink':'']"> <p @click='index=1'>新闻列表</p></router-link>
                <router-link tag='li' :to='{name:"weater"}'  :class="[index==2?'pink':'']"> <p @click='index=2'>天气温度</p></router-link>
                <router-link tag='li' :to='{name:"woman"}'  :class="[index==3?'pink':'']"> <p @click='index=3'>美女图片</p></router-link>
            </ul>
        </div>
        <div class="center">
            <router-view/>
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      index: 0
    };
  }
};
</script>

<style scoped lang='less'>
.px2rem(@name, @px) {
  @{name}: @px / 75 * 1rem;
}
.header {
  .px2rem(height, 100);
  background: pink;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.list {
  display: flex;
  background: #fff;
  .px2rem(height, 50);
  .px2rem(padding-top, 30);
  .px2rem(padding-bottom, 30);
  border-bottom: 2px solid #f5f5f5;
  border-top: 1px solid #fff;
  li {
    font-size: 1.5em;
    font-weight: 800;
    flex: 1;
    text-align: center;
  }
}
.fixed {
  position: fixed;
  width: 100%;
  top: 0px;
  z-index: 2000;
  display: flex;
  flex-direction: column;
}
.pink {
  color: pink;
  transition: color 0.5s;
}
.center {
  .px2rem(margin-top, 230);
   height: 100%;
}
.logo {
  .px2rem(height, 50);
  .px2rem(width, 50);
}
.text {
  font-size: 1.5em;
  color: #fff;
  font-weight: 800;
  zoom: 1;
  .px2rem(margin-left, 50);
}
</style>



