<template>
    <div class="wrap">
        <vueWaterfallEasy @scrollReachBottom='init' :imgsArr='imgs'></vueWaterfallEasy>
    </div>
</template>

<script>
import vueWaterfallEasy from "vue-waterfall-easy";
export default {
  data() {
    return {
      imgs: [],
      index:1
    };
  },
  methods: {
    init() {
      this.$indicator.open();
      this.$http.get(`https://www.apiopen.top/meituApi?page=${this.index}`).then(res => {
        res.data.data.forEach(o => {
          o.src = o.url;
        });
        this.imgs = this.imgs.concat(res.data.data);
        this.index++;
        this.$indicator.close();
      });
    }
  },
  components: {
    vueWaterfallEasy
  },
  created() {
    this.init();
  }
};
</script>

<style scoped lang='less'>
.px2rem(@name, @px) {
  @{name}: @px / 75 * 1rem;
}
.wrap {
  height: 800px;
}
.flex {
  display: flex;
  img {
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
  }
}
image[lazy="loading"] {
  width: 40px;
  height: 300px;
  margin: auto;
}
</style>



