import Vue from 'vue'
import Router from 'vue-router'
import main from '@/components/main'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      component: require('@/components/menu/bar.vue').default,
      children: [{
        path: '/',
        name: 'menu',
        component: require('@/components/main.vue').default,
      },
      {
        path: '/admin',
        name: 'admin',
        component: require('@/components/administrivia/live.vue').default,
      }, {
        path: '/woman',
        name: 'woman',
        component: require('@/components/woman/photo.vue').default,
      }, {
        path: '/weater',
        name: 'weater',
        component: require('@/components/weater/weaters.vue').default,
      }]
    }
  ]
})



