<template>
    <div>
        <mt-button   style="width:100%"  @click='popupVisible=true' type="default">{{areo}}</mt-button>
        <mt-popup
            style="width:100%"
            v-model="popupVisible"
            position="bottom">
            <mt-picker style="width:100%"  @change='onValuesChange' :slots="city"></mt-picker>
        </mt-popup>
            <mt-popup
           style="width:100%"
          v-model="tops"
          :modal='false'
          position="top">
            <div class="tops">
                {{error}}
            </div>
          </mt-popup>
          <div class="ec" ref="echarts">
          </div>
          <div class="msg">
             {{msg}}
          </div>
    </div>
</template>

<script>
import cityStr from "./index.js";
export default {
  data() {
    return {
      city: [{ values: cityStr.map(o => o.name) }],
      popupVisible: false,
      areo: "请选择地区",
      tops: false,
      error: "",
      locations: [],
      msg:''
    };
  },
  watch: {
    popupVisible(newVal, oldVal) {
      if (newVal == false) {
        this.init();
      }
    }
  },
  methods: {
    onValuesChange(p, v) {
      this.areo = v[0];
    },
    echar(data,options) {
      console.log(options)
      let $value = data.map(o=>Number(o.high.match(/\d/ig).join('')))
      let date = data.map(o=>o.date)
      let dom = this.$echarts.init(this.$refs.echarts);
      dom.setOption({
        title: {
          text: `${options.data.city}`
        },
        tooltip: {
          trigger: "axis",
          formatter: function(params) {
            params = params[0];
            return (
              `${params.axisValue}<br>温度${params.value}°C`
            );
          },
          axisPointer: {
            animation: false
          }
        },
        xAxis: {
          type: "category",
          data:date
        },
        yAxis: {
          type: "value",
          boundaryGap: [0, "100%"],
          splitLine: {
            show: false
          }
        },
        series: [
          {
            name: "xxx",
            type: "line",
            showSymbol: false,
            hoverAnimation: false,
            data: $value
          }
        ]
      });
    },
    init() {
      this.$indicator.open();
      this.$http
        .get(`https://www.apiopen.top/weatherApi?city=${this.areo}`)
        .then(res => {
          if (res.data.code != 200) {
            this.tops = true;
            this.error = res.data.msg;
            this.msg = ``;
            setTimeout(_ => {
              this.tops = false;
            }, 1500);
          } else {
            console.log(res.data.data);
            this.locations = res.data.data.forecast;
            this.echar(this.locations,res.data)
            this.msg = res.data.data.ganmao
          }
          this.$indicator.close();
        });
    }
  },
  beforeRouteLeave(to, from, next) {
    this.tops = false;
    next(true);
  },
  mounted() {
     this.init()
  }
};
</script>

<style socped lang='less'>
.px2rem(@name, @px) {
  @{name}: @px / 75 * 1rem;
}
.tops {
  font-size: 1.5em;
  width: 100%;
  text-align: center;
  .px2rem(height,100);
  background: #000;
  opacity: 0.5;
  color: white;
  .px2rem(line-height,100);
}
.ec{
  width: 100%;
  .px2rem(height,500);
}
.msg{
  font-size: 1.5em;
  padding: 20px;
}
</style>



