<template>
  <div>
    <mt-loadmore ref='loadmore'  :top-method="loadTop">
      <header class="maxbox" :key='index' v-for='(item,index) in list'>
          <div class="header">
            <img class="portrait" v-lazy="item.header" alt="">
            <p class="ceil">
              <span class="userName">{{item.username}}</span>
            <span>{{item.passtime}}</span></p> 
            </div> 
          <!-- <img :src="item.image"> -->
             <p class="context">{{item.text}} {{item.top_commentsContent}} </p>
             <img v-if='!item.video' class="superImg" :src="item.gif?item.gif:item.thumbnail" alt="">
             <video width="100%" class="videos" controls v-if='item.video' :src="item.video"></video>
             <div class="show">
                  <p @click='comment(item.soureid,index,item.is)'  class="centers" v-show='item.is'>点击查看评论</p> 
                  <ul>
                    <li class="lists" :key='i' v-for='(subitem,i) in item.com'>
                         <aside class="aside">
                             <img class="ports"  v-if='subitem.user' v-lazy="subitem.user.profile_image" alt=""> <p class="alt"> <span>{{subitem.user?subitem.user.username:''}}</span>
                             <span class="spans">{{subitem.user?subitem.user.sex=="m"? '♂' :'♀':''}}</span></p>
                         </aside>
                        <p class="text"> {{subitem.content? subitem.content :'没有评论'}}</p>
                        <div class="parents" v-show='subitem.user'> <p class="dl" > <img class="godjob" src="static/img/good.png" alt=""> <span class="good">{{subitem.like_count?subitem.like_count:0}}</span></p></div>
                    </li>
                  </ul>
             </div>
      </header>
      </mt-loadmore>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      comments: []
    };
  },
  methods: {
    loadTop(id) {
      this.init().then(res => {
        this.list = res;
        this.$refs.loadmore.onTopLoaded();
        this.$indicator.close()
      });
    },
    init() {
      this.$indicator.open();
      return new Promise((resovle, reject) => {
        this.$http
          .get(`https://www.apiopen.top/satinGodApi?type=1&page=1`)
          .then(res => {
            res.data.data.forEach(o => ((o.com = []), (o.is = true)));
            resovle(res.data.data);
          });
      });
    },
    comment(id, index) {
      this.$http
        .get(`https://www.apiopen.top/satinCommentApi?id=${id}&page=1`)
        .then(res => {
          if (res.data.data.normal.info.count == 0) {
            this.list[index].com = [{ content: "暂无评论" }];
          } else {
            this.list[index].com = res.data.data.normal.list;
          }
          console.log(this.list[index].com)
          this.list[index].is = false;
        });
    }
  },
  created() {
    this.init().then(res => {
      this.list = res;
      this.$indicator.close()
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='less'>
* {
  padding: 0;
  margin: 0;
}
.px2rem(@name, @px) {
  @{name}: @px / 75 * 1rem;
}
.maxbox {
  border-bottom: 10px solid #f5f5f7;
  .header {
    display: flex;
    align-items: center;
    .userName {
      color: rgb(20, 158, 255);
      font-weight: 800;
    }
    .ceil {
      display: flex;
      flex-direction: column;
      .px2rem(margin-left, 30);
      font-size: 14px;
    }
    .px2rem(margin-top, 100);
    .portrait {
      .px2rem(width, 100);
      .px2rem(height, 100);
      .px2rem(margin-left, 30);
      border-radius: 50%;
    }
    .videos {
      width: 100%;
    }
  }
  .superImg {
    width: 100%;
  }
}
.show {
  font-size: 15px;
  // text-align: center;
  font-weight: 800;
  .px2rem(margin-bottom, 30);
}
.context {
  font-size: 1.5em;
  .px2rem(margin-top, 50);
  .px2rem(margin-left, 30);
  .px2rem(margin-right, 30);
  .px2rem(margin-bottom, 50);
  line-height: 30px;
}
.aside {
  display: flex;
  align-items: center;
  .ports {
    .px2rem(margin-top, 30);
    .px2rem(width, 100);
    .px2rem(height, 100);
    border-radius: 50%;
    .px2rem(margin-left, 30);
  }
  span {
    .px2rem(margin-left, 30);
    color: rgb(20, 158, 255);
    font-weight: 800;
  }
}
.text {
  .px2rem(margin-top, 30);
  .px2rem(margin-left, 30);
  .px2rem(margin-right, 30);
  display: flex;
  .px2rem(margin-bottom, 20);
}
.lists {
  border-bottom: 3px solid #f5f5f7;
}
.centers {
  text-align: center;
  .px2rem(margin-top, 30);
}
.lists:nth-child(1) {
  border-top: 3px solid #f5f5f7;
}
.alt {
  display: flex;
  .px2rem(margin-top, 25);
  flex-direction: column;
  justify-content: space-around;
  .spans {
    .px2rem(margin-top, 20);
  }
}

.parents{
   display: flex;
   justify-content: flex-end;
   width: 100%;
    .px2rem(margin-bottom, 20);
   .dl{
      .px2rem(margin-right, 40);
      display: flex;
      align-items: center;
      justify-content: space-around;
      width: 13%;
   }
   .godjob{
  .px2rem(height, 40);
  .px2rem(width, 40);
}
.good{
  font-weight: 100;
  color: #333;
}
}
</style>



