<template>
    <div>
    <div class="tops">
        <mt-swipe :auto="4000">
        <mt-swipe-item :key='index' v-for='(item,index) in swiper[0].picInfo'>
            <img class="f_ui_img" v-lazy="item.url" alt="">
        </mt-swipe-item>
        </mt-swipe>
    </div>
    <ul>
        <li class="wrap" :key='index' v-for='(item,index) in auto'>
            <div :class="[item.imgsrc3gtype == 2?'title':'title_two']">
                <h3 class="text"> <span>{{item.title}}</span> <span v-if='item.imgsrc3gtype == 1' class="detail">{{item.tcount}}跟贴 {{item.ptime | timer}}小时之前</span></h3>
                <div class="flex">
                    <img :key='i' v-for='(subitem,i) in item.picInfo'  v-lazy="subitem.url">
                </div>
                <p class="detail2"  v-if='item.imgsrc3gtype == 2'><span>{{item.tcount}}跟贴 {{item.ptime | timer}}小时之前</span></p>
            </div>
        </li>
    </ul>
    </div>
</template>

<script>
export default {
  props: ["swiper","auto"],
  data() {
    return {};
  },
  filters: {
    timer(option) {
      let date = new Date().getTime();
      let oldTime = new Date(option).getTime();
      let minus = date - oldTime;
      let lavel = minus % (24 * 3600 * 1000);
      let hours = lavel / (3600 * 1000);
      return Math.floor(hours);
    }
  }
};
</script>

<style scoped lang='less'>
.px2rem(@name, @px) {
  @{name}: @px / 75 * 1rem;
}
.wrap {
  border-bottom: 2px solid #e5e5e5;
  .px2rem(padding, 20);
  .detail2 {
    color: #b4b4b4;
    font-size: 13px;
    .px2rem(margin-top, 20);
  }
}
.title {
  display: flex;
  flex-wrap: wrap;
  .text {
    font-size: 1.5em;
    display: block;
    font-weight: normal;
    max-height: 0.9rem;
    overflow: hidden;
    line-height: 0.45rem;
  }
  .flex {
    display: flex;
    justify-content: flex-end;
    width: 100%;
    .px2rem(margin-top, 20);
    img {
      display: block;
      flex: 0 0 33.1%;
      .px2rem(width, 210);
      .px2rem(height, 140);
    }
  }
}
.title_two {
  display: flex;
  .text {
    width:65%;
    font-size: 1.5em;
    display: block;
    font-weight: normal;
    max-height: 0.9rem;
    line-height: 0.45rem;
    display: flex;
    flex-direction: column;
    .px2rem(margin-right, 40);
    .px2rem(height, 140);
    .detail {
      color: #b4b4b4;
      font-size: 13px;
      .px2rem(margin-top, 20);
    }
  }
  .flex {
    display: flex;
    img {
      display: block;
      .px2rem(width, 210);
      .px2rem(height, 140);
    }
  }
}
.tops {
  width: 100%;
  .px2rem(height, 300);
  .f_ui_img {
    width: 100%;
    height: 100%;
  }
}
</style>



